import React, {useState, useEffect} from 'react'
import {Row, Col} from 'react-bootstrap'
import flexing from '/Users/ayush/Desktop/JavaScript/React/website/src/flexing.gif'


function ContactPage(){
    const [email, setEmail] = useState("")
    const [subject, setSubject] = useState("")
    const [info, setInfo] = useState("")

    //1065189501683-8o2cch7qaqua7ib1uiitn849kadbh5gd.apps.googleusercontent.com
    //AIzaSyBEM-Qq-RGGXWzPUWJIeDBN3QGHKkHdaSQ
    function handleSubmit(event){
        event.preventDefault();
        const templateId = 'template_ALJ29pyc';
        sendFeedback(templateId, {message_html: info, from_email: email, subject: subject})
        setEmail("");
        setSubject("");
        setInfo("");
    }
    function sendFeedback (templateId, variables) {
        window.emailjs.send(
          'gmail', templateId,
          variables
          ).then(res => {
            console.log('Email successfully sent!')
          })
          .catch(() => alert("Email unsuccessfully sent..."))
      }

    return(
        <div className="contact-page" style={{height:"70%"}}>
            <Row style={{marginTop:""}}>
                <Col style={{height:"60vh", marginTop:"10%", marginLeft:"5%", margin:"auto"}}>
                    <div className="contactmeStart" style={{color:"#22204b", justifyContent:"center"}}>Shoot me a message!</div>
                    <form action="/Users/ayush/Desktop/JavaScript/React/website/src/sendEmail.php" style={{ margin:"0 auto", width:"100%", height:"100%"}}>
                        <input value={email} name="email" onChange={event => setEmail(event.target.value)} type="text" style={{width:"40%", backgroundColor:"rgba(90, 200, 204, 0.3)", marginLeft:"30px", marginTop:"20px"}} placeholder="Enter your email here..."/>
                        <input value={subject} name="subject" onChange={event => setSubject(event.target.value)} type="text" style={{width:"70%", backgroundColor:"rgba(90, 200, 204, 0.3)", marginLeft:"30px", marginTop:"20px"}} placeholder="Enter your subject here..."/>
                        <textarea value={info} name="info" onChange={event => {setInfo(event.target.value)}} type="textarea" style={{width:"70%", height:"40%", backgroundColor:"rgba(90, 200, 204, 0.3)", marginLeft:"30px", marginTop:"20px"}} placeholder="What's up?"/>
                        <div style={{paddingTop:"10px", marginLeft:"30px"}}><input type="submit" onClick={handleSubmit} style={{backgroundColor:"rgba(90, 200, 204, 0.3)", border:"5px", borderColor:"black"}}></input></div>
                    </form>
                </Col>
                <Col style={{height:"60vh", margin:'0 auto'}}>
                    <div >
                        <img src={flexing} alt="loading..." style={{height:"60vh"}}/>
                    </div>
                </Col>
            </Row> 
        </div>
    )
}
export default ContactPage;