import React, {useState, useEffect, useLayoutEffect} from 'react'
import InfiniteScroll from 'react-infinite-scroller';
import {Button, Row} from 'react-bootstrap'
import { wrap } from 'module';
import GitHubCard from './GitHubCard'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'
import { faHandPointLeft, faHandPointRight } from '@fortawesome/free-regular-svg-icons'




function Githubapps(){

    const [data, setData] = useState([])
    const [currentIndexes, setCurrentIndexes] = useState([]);
    const [loaded, setLoaded] = useState(false)

    useEffect(() => {
        fetch('https://api.github.com/users/ayushnpatel/repos')
        .then(res => res.json())
        .then(info => {
            let i = 0;
            info = info.map((item) =>{
                item.index = i++;
                return item;
            })
            return setData(info)
        }).catch(err => setData({error:err}))
        setLoaded(true);
        
    }, []);
    useEffect(() => {
        setCurrentIndexes(() => {
            return [0, 1, 2];
        })
    }, [data])

    function nextCards(){
        setCurrentIndexes(prevIndexes => {
            let indexArray = prevIndexes.map(item => {
                 return item + 2 >= data.length ? item : item+1;
            })
            return indexArray;
        })
    }
    function prevCards(){
        setCurrentIndexes(prevIndexes => {
            let indexArray = prevIndexes.map(item => {
                 return item - 2 < 0 ? item : item-1;
            })
            return indexArray;
        })
    }

    return (  
        <div style={{paddingTop:"20vh"}}>
            <Row >
                <FontAwesomeIcon className="cardLeftButton" style={{margin:'auto', width:'100px', height:'100px', color:"#434261", marginRight:"8px"}} icon={faHandPointLeft} onClick={() => prevCards()}></FontAwesomeIcon>
                <div className="cardSliderWrapper" style={{display:'flex', maxWidth:'142vh', margin:'0 auto', overflowX:'hidden'}}>
                    <div className="cardHolder"style={{marginLeft:"47.4vh", position:'relative', display:'flex', 'transform': `translateX(-${currentIndexes[1]*(100/data.length)}%)`}}>
                            {data.map(item => {
                                return item ? (
                                    <GitHubCard key={item.index} item={item}></GitHubCard>
                                ) : <div></div>
                            })}                        
                    </div>
                </div>    
                <FontAwesomeIcon className="cardRightButton" style={{margin:'auto', width:'100px', height:'100px', color:"#434261", marginLeft:"10px"}} icon={faHandPointRight} onClick={() => nextCards()}></FontAwesomeIcon>

            </Row>
            
            
        </div>
    ) 
}

export default Githubapps;
