import React from 'react'
import {Card, Row} from 'react-bootstrap'
import '/Users/ayush/Desktop/JavaScript/React/website/src/stylesheets/main.css'
import { faJava, faJs, faHtml5, faGithub} from '@fortawesome/free-brands-svg-icons'
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome'


function GitHubCard(props){
    let {name, description, html_url, language} = props.item;
    name = name.split('-').join(" ");
    let icon;
        switch(language){
            case "Java":
                icon = faJava;
                break;
            case "JavaScript":
                icon = faJs;
                break;
            case "HTML":
                icon = faHtml5;
                break;
            default:
                icon = faGithub;
                break;
        }
    
    const cardStyle={
        height:"86%",
        width:"45vh",
        boxShadow:"10px 5px 10px rgba(136, 140, 125, 0.93)",
        borderRadius: "18px"
    }
    return (
        <div style={{margin:'10px'}}>
            <Card style={cardStyle}
                  bg="light">
                <Card.Header style={{textAlign:"center", backgroundImage:"linear-gradient(to right, #72b1b3ee,#72b1b3be ,#72b1b3e1)", borderTopLeftRadius:"18px", borderTopRightRadius:"18px"}}>{name}</Card.Header>
                <Card.Body style={{textAlign:"center"}}>
                        <Row style={{justifyContent:"center", height:"150px"}}>{description}</Row>
                        <Row style={{justifyContent:"center", height:"150px"}}><FontAwesomeIcon style={{color: '#434261'}} size='4x' icon={icon}></FontAwesomeIcon></Row>
                </Card.Body>
                <Card.Footer style={{textAlign:"center", backgroundColor:"#72b1b3", borderBottomLeftRadius:"18px", borderBottomRightRadius:"18px"}}>
                    <a target="_blank" className="link" href={html_url}>More</a>
                </Card.Footer>
            </Card>
        </div> 
    )
}
export default GitHubCard;